body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 1s linear;
          animation: App-logo-spin infinite 1s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #182d32;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Card {
    transition: all 0.7s, z-index 0s;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    position: absolute;
    top: 50vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}


.textBlur {
    color: red;
    -webkit-filter: blur(3px);
            filter: blur(3px)
}

.textClear {
    color: white;
    -webkit-filter: blur(0px);
            filter: blur(0px)
}

.cardText, .cardTitle {
    position: absolute;
}

.cardTitle {
    top: 20%;
    text-align: center;
    font-family: 'ribeye', 'chelsea market';
    color: rgb(255, 235, 209);
    text-shadow: 5px 5px 8px black, -5px -5px 8px black;

}
.cardText {
    top: 40%;
    text-align: center;
    width: 80%;
    height: 24%;
    font-family: 'ribeye', 'ranchers', 'sans-serif';
    font-weight: bolder;
    color: black;
    /* transition: font-size 10s; */
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.backgroundDiv {
    height: 100%;
    width: 100%;
    background-position: center;
    background-size: cover;
    border-radius: 15px;

}

.backgroundDiv, .cardText, .cardTitle {
    transition: all 0.7s;
}

#card02 .backgroundDiv{
    background-position: 25% center;
}
.Home {
    background-image: url(/testsite/static/media/tileable_wood_texture.d5098b2c.png);
    background-repeat: repeat-x repeat-y;
    width: 100vw;
    height: 100vh;
}

#navCardContainer {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    background-color: gray;
    color: black;
    -webkit-filter: drop-shadow(20px 20px 3px rgba(2, 2, 2, 0.5)) drop-shadow(-20px -20px 3px rgba(2, 2, 2, 0.5));
            filter: drop-shadow(20px 20px 3px rgba(2, 2, 2, 0.5)) drop-shadow(-20px -20px 3px rgba(2, 2, 2, 0.5))
}
.contact-page {
    display: flex;
    align-items: center;
    justify-content: center;
}
.contact-card {
    background-image: url(/testsite/static/media/paper.e8849546.png);
    background-size: 100%;
    width: 40%;
    font-family:'special elite', 'permanent marker', monospace;
    position: absolute;
    top: 20%;
    -webkit-transform: rotate(-3deg) rotateY(-45deg);
            transform: rotate(-3deg) rotateY(-45deg);
    -webkit-filter: drop-shadow(10px 10px 20px rgba(250, 250, 250, 0.2));
            filter: drop-shadow(10px 10px 20px rgba(250, 250, 250, 0.2));
    /* transition: all 2s cubic-bezier(0.5, 0, 0.5, 1); */
    display: flex;
    justify-content: center;

}

.contact-info {
    width: 100%;
    margin: 10%;
    -webkit-transform: rotate(6deg);
            transform: rotate(6deg);
    -webkit-transform-origin: center;
            transform-origin: center;
    color: rgba(49, 49, 49, 0.8);
    text-shadow: 1px 1px 0px rgb(255, 255, 231), -1px -1px 0px rgb(42, 42, 0);
    /* filter: drop-shadow(10px 10px 2px teal); */
    /* transition: all 2s cubic-bezier(0.5, 0, 0.5, 1); */
    /* transition: all 2s cubic-bezier(0.5, 0, 1, 0.5); */

}
.pullDownBar {
    position: fixed;
    width: 100vw;
    height: 20%;
    background-image: url(/testsite/static/media/navplank-small.e35a5d0e.png);
    background-position: center;
    background-size: cover;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    transition: all 0.6s cubic-bezier(0.35, -0.57,0.32, 1.45);
    -webkit-filter:  drop-shadow(0px 16px 3px rgba(2, 2, 2, 0.5));
            filter:  drop-shadow(0px 16px 3px rgba(2, 2, 2, 0.5));
    z-index: 8;
}

.navLink {
    position: relative;
    top: 10%;
    color: rgba(200, 250, 200, 0.5);
    font-size: 2em;
    text-shadow: 15px 5px 15px black, -15px 5px 15px black;
    text-decoration: none;
    transition: all 1s;
    font-family: 'ribeye', monospace;
}

.navLink:hover {
    color: rgba(200, 250, 200, 0.8);

    text-shadow:1px 1px 5px rgba(250, 250, 250), -1px 1px 5px rgba(250, 250, 250, 0.5);

    text-decoration: none;

}
.placeholder-page {
    height: 100vh;
    background-color: #222;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.hourglass-image {
    margin-top: 30vh;
    width: 25%;
    -webkit-filter: drop-shadow(10px 10px 8px rgba(0, 0, 0, 0.6));
            filter: drop-shadow(10px 10px 8px rgba(0, 0, 0, 0.6))
}

.placeholder-message {
    font-size: 2em;
    color: white;
    font-family: 'ribeye', 'chelsea market', sans-serif;
    text-shadow: 10px 10px 8px rgba(0, 0, 0, 0.6);
    margin-bottom: 25%;
width: 100%;
}
